@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
* {
    margin: 0;
    padding: 0;
}

.wrapper {
    overflow-x: hidden;
    overflow-y: auto;
}

.error-msg {
    color: red;
    font-size: 12px;
    font-weight: 600;
}

@media(max-width: 768px) {
    .mybtn {
        width: 100%;
    }
}

html,
body {
    width: 100%;
    height: 100vh;
    font-family: 'Open Sans', serif;
}

img {
    vertical-align: middle;
    border-style: none;
    width: 100%;
}


@media(max-width: 768px) {
    .slideer {
    border-style: none;
    width: 300%;
    display: block;
    margin-left: -100%;
    margin-right: auto;
    }
}

/* navbar */
.navbox {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    /* align-items: center; */
    padding: 30px;
    /* flex-direction: column; */
    width: 1100px;
}

@media(max-width: 768px) {
    .navbox {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    /* align-items: center; */
    padding: 30px;
    /* flex-direction: column; */
    width: 460px;
    }
}

.navbox .leftside {
    width: 56px;
    height: 56px;
}

.navbox .leftside img {
    width: 200%;
    height: 50%;
}

.navbox .rightside {
    font-size: 12px;
    font-weight: 600;
    position: relative;
}

.navbox .rightside span {
    margin-right: 20px;
}

.navbox .rightside span .navlink {
    text-decoration: none;
    color: #000;
}

.navbox .rightside .no-of-products {
    background: #EF8121;
    color: #fff;
    font-size: 10px;
    font-weight: 600;
    padding: 2px;
    width: 14px;
    height: 14px;
    border-radius: 3px;
    position: absolute;
    top: -5px;
    left: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logout-btn {
    padding: 5px;
    border-radius: 3px;
    color: #fff;
    font-size: 12px;
    font-weight: 600;
    cursor: pointer;
    outline: none;
    border: hidden;
    background-color: #1C486F;
    margin-left: 10px;
}

/* products */

h1 {
    padding: 30px;
    border-bottom: 1px solid #e4e4e4;
}

@media(max-width: 768px) {
    h1 {
        text-align: center;
        padding: 10px;
    }
}

.products-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    flex-wrap: wrap;
}

.products-container .product-card {
    width: 300px;
    height: auto;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    font-size: 12px;
    font-weight: 600;
    text-align: center;
    box-shadow: 5px 5px 4px #e4e4e4;
    position: relative;
    border-radius: 10px;
}

.products-container .product-card .product-img {
    width: 100%;
    height: 100%;
}

.products-container .product-card .product-img img {
    width: 100%;
    height: 100%;
}

.products-container .product-card .product-name {
    display: block;
    width: 100%;
    color: #1C486F;
    margin-top: 5px;
}

.products-container .product-card .product-price {
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
}

.products-container .product-card .addcart-btn {
    background-color: #EF8121;
    color: #fff;
    border: hidden;
    cursor: pointer;
    width: 100%;
    padding: 10px 14px;
    margin-top: 5px;
    margin-bottom: 2px;
    outline: none;
    border-radius: 10px;
}

/* cart */
.cart-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    font-size: 14px;
    font-weight: 600;
}

.cart-container .cart-card {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 20px;
}

@media(max-width: 768px) {
    .cart-container .cart-card {
        flex-direction: column;
        justify-content: center;
        border-bottom: 1px solid #e4e4e4;
    }
}

.cart-container .cart-card .cart-img {
    width: 150px;
    height: 120px;
}

@media(max-width: 768px) {
    .cart-container .cart-card .cart-img {
        width: 90%;
        height: auto;
    }
}

.cart-container .cart-card .cart-img img {
    width: 100%;
    height: 100%
}

.cart-container .cart-card .cart-name,
.cart-container .cart-card .cart-price {
    width: 150px;
    margin-left: 20px;
}

@media(max-width: 768px) {

    .cart-container .cart-card .cart-name,
    .cart-container .cart-card .cart-price {
        width: 90%;
        margin-left: 0px;
        text-align: center;
        margin-top: 4px;
    }
}

.cart-container .cart-card .cart-price-orignal {
    width: 150px;
    margin-left: 20px;
    color: #EF8121;
}

@media(max-width: 768px) {

    .cart-container .cart-card .cart-price-orignal {
        width: 90%;
        margin-left: 0px;
        text-align: center;
        margin-top: 4px;
    }
}

.delete-btn {
    background-color: transparent;
    border: hidden;
    outline: none;
    cursor: pointer;
    color: red;
}

.inc,
.dec {
    cursor: pointer;
}

@media(max-width: 768px) {
    .delete-btn {
        margin-top: 4px;
    }

    .inc,
    .dec {
        margin-top: 4px;
    }
}

/* cart summary */
.cart-summary {
    display: flex;
    flex-direction: column;
    padding: 50px;
    border: 1px solid #e4e4e4;
    width: 300px;
}

.cart-summary .cart-summary-heading {
    border-bottom: 1px solid #EF8121;
}

.cart-summary .cart-summary-price {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.cashout-link {
    font-size: 12px;
    font-weight: 600;
    text-decoration: none;
    color: #000;
}

/* cashout */
.success-msg {
    background-color: green;
    color: #fff;
    padding: 7px;
    font-size: 12px;
    font-weight: 600;
}

.success-msg .home-link {
    color: #fff;
}


.contact-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #EF8121;
    padding: 20px;
}

.contact-heading {
    color: #ffffff;
    margin-bottom: 10px;
}

.contact-info {
    color: #e3e3e3;
    margin-bottom: 5px;
}

.contact-link {
    color: #007bff;
    text-decoration: none;
}

.whatsapp-button {
    background-color: #25D366; /* WhatsApp color */
    color: white; /* Text color */
    border: none; /* Remove default button border */
    border-radius: 5px; /* Rounded corners */
    padding: 10px 20px; /* Some padding */
    text-decoration: none; /* Remove default link underline */
    font-size: 16px; /* Increase font size */
    display: inline-block; /* Necessary for adding margins and specific width/height */
    transition: all 0.3s ease 0s; /* Smooth transition for hover effect */
    cursor: pointer; /* Cursor pointer on hover */
    align-items: center; /* Center the content (icon and text) */
    justify-content: center; /* Center the content (icon and text) */
}

.whatsapp-button:hover {
    background-color: #128C7E; /* Darken the background color on hover */
}

.whatsapp-button i {
    margin-right: 5px; /* Add some space between the icon and the text */
}

/* Signup.css */

.signup-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1c486f;
}

.signup-form {
    background: #ffffff;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 600px;
    text-align: center;
}

@media(max-width: 768px) {
    .signup-form {
        background: #ffffff;
        padding: 30px;
        border-radius: 10px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        width: 400px;
        text-align: center;
    }
}

.signup-form h2 {
    margin-bottom: 20px;
}

.signup-form label {
    display: block;
    text-align: left;
    margin-bottom: 5px;
}

.signup-form input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.btn-submit {
    width: 100%;
    padding: 10px;
    background-color: #ee8122;
    border: none;
    border-radius: 5px;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
}

.btn-submit:hover {
    background-color: #357ABD;
}

.social-login {
    margin: 20px 0;
}

.social-login button {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: none;
    border-radius: 5px;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
}

.btn-google {
    background-color: #db4437;
}

.btn-google:hover {
    background-color: #c23321;
}

.btn-facebook {
    background-color: #3b5998;
}

.btn-facebook:hover {
    background-color: #2d4373;
}

.error-msg {
    color: red;
    margin-bottom: 15px;
}

.logo {
    width: 50%;
}


.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1c486f;
}

.login-form {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    width: 600px;
    text-align: center;
}

@media(max-width: 768px) {
    .login-form {
        background-color: white;
        padding: 30px;
        border-radius: 10px;
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
        width: 400px;
        text-align: center;
    }
    
}


.login-form h2 {
    margin-bottom: 20px;
}

.login-form form {
    display: flex;
    flex-direction: column;
}

.login-form input[type="email"],
.login-form input[type="password"] {
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
}

.login-form .remember-me {
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.login-form .remember-me input {
    margin-right: 5px;
}

.login-btn {
    background-color: #ee8122;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}

.login-btn:hover {
    background-color: #2563EB;
}

.alternative-login {
    display: flex;
    flex-direction: column;
    margin: 20px 0;
}

.google-btn, .facebook-btn {
    background-color: #DB4437; /* Google Red */
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 5px 0;
}

.facebook-btn {
    background-color: #4267B2; /* Facebook Blue */
}

.google-btn:hover {
    background-color: #C53727;
}

.facebook-btn:hover {
    background-color: #365899;
}

.error-msg {
    color: red;
    margin-top: 10px;
}

.login-form span {
    display: block;
    margin-top: 20px;
}

.logo {
    width: 50%;
}
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f8f8f8;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 600px;
    margin: auto;
    margin-top: 50px;
}

h2 {
    margin-bottom: 20px;
}

.form-group {
    width: 100%;
}

label {
    margin-top: 20px;
    font-weight: bold;
    display: block;
}

input {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.btn {
    background-color: #5cb85c;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    margin-top: 20px;
    cursor: pointer;
}

.btn:hover {
    background-color: #4cae4c;
}

.error-msg {
    color: red;
    margin-top: 20px;
    display: block;
}

